<template>
    <div v-if="lists && !submitted">
        <div class="inputBox">
                <label for="title">Title</label>
                <input 
                    type="text"
                    id="title"
                    required
                    v-model="lists.title"
                    name="title"
                />
            </div>
            <div class="inputBox">
                <label for="contents">contents</label>
                <textarea 
                    type="text"
                    id="contents"
                    required
                    v-model="lists.contents"
                    name="contents"
                    rows="4"
                    cols="80"
                    placeholder="put contents"
                ></textarea>
            </div>
            <button class="buttons" @click.prevent="saveLists">Save</button>
    </div>
    <div v-else-if="submitted">
        <h1 @click="defaultThings">submitted</h1>
    </div>
    <div v-else>
        <h1 @click="getDatasById(ids)">Notthing here</h1>
        <!-- @click에는 조건문 못 넣어 -->
    </div>
</template>

<script>
import ds from '@/services/DataServices.js'
    export default{
        name:"details",
        data(){
            return{
                lists : null,
                ids : this.$route.params.id,
                submitted :false
            }
        },
        methods : {
            getDatasById(id){
                if(id){
                ds.getById(id)
                    .then(res=>{
                        this.lists = res.data;
                    })
                    .catch(e=>{
                        console.log(e)
                    })
                }
            },
            saveLists(){            
            ds.update(this.ids, this.lists)
                .then(res=>{
                    console.log(res.data)
                    this.submitted = true;                    
                })
                .catch(e=>{
                    console.error(e.response.data)  
                })

        },
            defaultThings(){
                this.submitted = false;
                this.getDatasById(this.$route.params.id);
            }

        },
        mounted(){
            this.getDatasById(this.$route.params.id);
        }
    }

</script>