<template>
    <div id="addBox">
        <div v-if="!submitted && !gotError">
            <div class="inputBox">
                <label for="title">Title</label>
                <input 
                    type="text"
                    id="title"
                    required
                    v-model="lists.title"
                    name="title"
                />
            </div>
            <div class="inputBox">
                <label for="contents">contents</label>
                <textarea 
                    type="text"
                    id="contents"
                    required
                    v-model="lists.contents"
                    name="contents"
                    rows="4"
                    cols="80"
                    placeholder="put contents"
                ></textarea>
            </div>
            <button class="buttons" @click.prevent="saveLists">Save</button>
        </div>
        <div v-else-if="submitted && !gotError">
            <h1>Saved well</h1>
            <button class="buttons" @click="newAddLists">add more</button>
        </div>
        <div v-else @click="newAddLists">
            <h1>Got some Error</h1>
            <h4>{{ msg }}</h4>
        </div>
    </div>

</template>
<script>
import ds from '@/services/DataServices.js'
export default {
    name: "add",
    data() {
        return {
            submitted: false,
            gotError: false,
            msg: "",
            lists: {
                title: "",
                contents: ""
            }
        }
    },
    methods : {
        saveLists(){
            let newLists = {
                title : this.lists.title,
                contents : this.lists.contents
            };
            console.log(newLists)
            ds.create(newLists)
                .then(res=>{
                    console.log(res.data)
                    this.submitted = true;                    
                })
                .catch(e=>{
                    console.error(e.response.data)
                    this.gotError = true;
                    this.msg = e;                   
                })

        },
        newAddLists(){
            this.lists = {};
            this.msg = "";
            this.submitted = false;
            this.gotError = false;
        }
    }
}
</script>